

import React, { useEffect, useState } from 'react';
import { useLocation } from "react-router-dom";
import movieTrailer from 'movie-trailer';
import ReactPlayer from "react-player"
import styles from './MovieDetails.module.css';
import { useNavigate } from 'react-router-dom';

function MovieDetails() {
    const navigate = useNavigate();
    let location = useLocation();
    let data = location.state;

    const [fetchedData, setFetchedData] = useState({
        name: '',
        adult: false,
        year: '',
        description: '',
        background: '',
        poster: '',
        score: 0,
        link: '',
    });

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        const fetchLink = async () => {
            try {
                setLoading(true);
                const fetchedLink = await movieTrailer(null, { tmdbId: data.id })
                let newData = {
                    name: data.title,
                    adult: data.adult,
                    year: data.release_date.slice(0, 4),
                    description: data.overview,
                    background: `https://image.tmdb.org/t/p/original/${data.backdrop_path}`,
                    poster: `https://image.tmdb.org/t/p/original/${data.poster_path}`,
                    score: data.vote_average * 10,
                    link: fetchedLink,
                }

                setFetchedData(newData)
                setLoading(false);
            }
            catch (error) {
                console.log(error)
            }
        }
        fetchLink();
    }, [])

    let Rating = '';
    let Runtime = '';
    if (data.IMDBdata) {
        Rating = data.IMDBdata.rating;
        Runtime = data.IMDBdata.runtime;
    }

    const { name, year, adult, description, background, poster, score, link } = fetchedData;

    return (
        <div className={styles.papa} style={{ backgroundImage: 'url(' + background + ')' }}>
            <div className={styles.fixedBackArrow}><img title='Back' src={require('../assets/back.png')} onClick={() => navigate(-1)} className={styles.backArrow}></img></div>
            {
                loading
                    ? <div className={styles.loadingContainer}><img src={require('../assets/loading.gif')} className={styles.imageLoading}></img></div>
                    : <div className={styles.container}>
                        <div className={styles.childContainer}>
                            <div className={styles.flexContainer}>
                                <div className={styles.textContainer}>
                                    <h1 className={styles.header}>{name} ({year})</h1>
                                    <div className={styles.row}>
                                        <div className={styles.tags}>
                                            {
                                                adult ? <div className={styles.extra}>R | </div > : <div className={styles.extra}>PG</div>
                                            }
                                            <div className={styles.extra}>|</div>
                                            {
                                                Runtime.length > 0 ? <div>{Runtime}</div> : <></>
                                            }
                                        </div>
                                        {
                                            Rating.length > 0 ? <p className={styles.starContainer}><img className={styles.star} src={require('../assets/star.png')} />IMDB: <div className={styles.extra2}>{Rating}</div></p> : <></>
                                        }
                                        <p className={styles.starContainer}>Metascore: <div className={styles.border} style={{
                                            backgroundColor: score < 33 ? 'red' : score < 66 ? 'orange' : 'green',
                                        }}   >{score}</div></p>
                                    </div>

                                    <h2>Overview</h2>
                                    <p>{description}</p>
                                </div>
                                <div className={styles.videoContainer}>
                                    <ReactPlayer width='100%' height='100%' controls={true} url={link}></ReactPlayer>
                                </div>
                            </div>
                        </div>
                    </div>

            }
        </div>
    );
}

export default MovieDetails;
