import React from 'react';
import Fade from 'react-reveal/Fade';
import { Link } from "react-router-dom";
import styles from './MovieBanner.module.css';

function MovieBanner({ data }) {
    return (
        <Fade>
            <div className={styles.posterContainer}>
                <Link className={styles.linker} to={{ pathname: `/movie/${data.title}` }} state={data}>
                    <img src={`https://image.tmdb.org/t/p/original/${data.poster_path}`} className={styles.poster}></img>
                    <h1 className={styles.posterHeader}>{data.title} ({data.release_date.slice(0, 4)})</h1>
                </Link>
            </div>
        </Fade >
    );
}

export default MovieBanner;
