
import React, { useEffect, useState } from 'react';
import MovieBanner from '../Components/MovieBanner';
import styles from './Movies.module.css';
import updateFetchData from '../hooks/fetchUpdate';

function Movies({ data, allFolders, setAllFolders, index }) {
    const [loading, setLoading] = useState(false);
    const onChange = (e) => {
        e.preventDefault();
        let folder = e.target.files;
        updateFetchData(allFolders, setAllFolders, folder, data.folderName, setLoading, index, data.id);
    }
    const [filteredData, setFilteredData] = useState(data);

    useEffect(() => {
        setFilteredData(data);
    }, [data])

    const filterData = (e) => {
        e.preventDefault();
        let term = e.target.value.toLowerCase();
        let newData = data.FinishedData.filter(item => item.title.toLowerCase().includes(term));
        let finished = {
            ...data,
            FinishedData: newData
        }
        setFilteredData(finished);
    }

    return (
        <div className={styles.Container}>
            <h1 className={styles.header}>{data.folderName}</h1>
            <div className={styles.containerInput}>
                <input placeholder='Search' type={'search'} onChange={(e) => filterData(e)} className={styles.searchInput}></input>
                <label title='Select Directory' htmlFor="file-input" className={styles.fileText}>
                    <img src={require('../assets/reload.png')} className={styles.reload}></img>
                    <div className='text'>Refresh Directory</div>
                </label>
                <input className={styles.disabledInput} type="file" id="file-input" required multiple onChange={(e) => onChange(e)} />
            </div>
            <div className={styles.MoviesContainer}>
                {
                    loading
                        ? <img src={require('../assets/loading.gif')} className={styles.imageLoading}></img>
                        : filteredData.FinishedData.map((item, index) =>
                            <MovieBanner key={index} data={item} index={index} />
                        )
                }
            </div>
        </div>

    );
}

export default Movies;