import React, { useEffect, useState } from 'react';
import { ContextFunction } from '../Context/ContextProvider';
import { Routes, Route, Link } from "react-router-dom";
import MovieDetails from '../Pages/MovieDetails';
import Movies from '../Pages/Movies';
import Settings from '../Pages/Settings';
import PageNotFound from '../Pages/PageNotFound';
import Header from './header';

function RouterComponent() {
    const obj = ContextFunction();
    const { allFolders, setAllFolders } = obj;

    return (
        <>
            <Header allFolders={allFolders} />
            {/* All possible routes */}
            <Routes>
                <Route path="" element={<Settings />} />
                <Route path="/MovieDetails" element={<MovieDetails />} />
                <Route path="*" element={<PageNotFound />} />
                {allFolders.map(
                    (item, index) => <Route path={`/${item.folderName}${item.id}`} exact key={index} element={<Movies data={item} allFolders={allFolders} setAllFolders={setAllFolders} index={index} />} />
                )}
                {allFolders.map(
                    (item, index) => <Route path={`/movie/:id`} exact key={index} element={<MovieDetails data={item.FinishedData} />} />
                )}
                {/* /movies/movie34993/movie.title */}
            </Routes >
        </>
    );
}

export default RouterComponent;