import { format } from 'date-fns';
import ls from 'local-storage';

export default async function updateFetchData(allFolders, setAllFolders, array, folderName, setLoading, index, smallId) {
    setLoading(true);
    if (folderName.length > 0 && array.length > 0) {
        await updateBoth(allFolders, setAllFolders, array, folderName, setLoading, index, smallId);
    } else if (folderName.length > 0) {
        await updateName(setAllFolders, allFolders, folderName, setLoading, index, smallId);
    } else if (array.length > 0) {
        await updateFolders(setAllFolders, allFolders, array, folderName, setLoading, index, smallId);
    }
    setLoading(false);
}

// Updates name and date
const updateName = async (setAllFolders, allFolders, folderName, setLoading, index, smallId) => {
    // Now finishedData is the list of movies from folders
    let temp_state = [...allFolders];
    let temp_element = { ...allFolders[index] };
    temp_element = { ...temp_element, folderName: folderName };
    temp_state[index] = temp_element;
    setAllFolders(temp_state);
    ls.set('movies', temp_state);
}

// Updates folders
const updateFolders = async (setAllFolders, allFolders, array, folderName, setLoading, index, smallId) => {
    // holds names from folders
    const Names = [];
    let Name;
    let Date;

    // Folder search for names and map to Names array
    for (let i = 0; i < array.length; i++) {
        Name = array[i].name.split('.')[0].split('(')[0];
        Date = array[i].name.split('.')[0].replace(/\D+/g, '');
        let trimmedName = Name.replace(/\s+$/, '');
        let trimmedDate = Date.replace(/\s+$/, '');
        let newData = {
            name: trimmedName,
            date: trimmedDate,
        }
        Names.push(newData);
    }
    // Gets unique array
    let unique = Names.filter((ele, ind) => ind === Names.findIndex(elem => elem.name === ele.name))

    // Holds ready to upload data
    let FinishedData = [];

    // Filters all items, pulls images and other data, and maps to FinishedData
    for (let i = 0; i < unique.length; i++) {
        try {
            const response = await fetch(`https://api.themoviedb.org/3/search/movie?query=${unique[i].name}&api_key=a3dbbc2d7d1c0929ba88f4887b98b5ca`);
            const data = await response.json();
            let newData = data.results.filter(Item => Item.release_date.includes(unique[i].date));
            if (newData.length !== 1) {
                newData = data.results.filter(Item => Item.title === unique[i].name && Item.release_date.includes(unique[i].date));
            }
            const IMDBdata = await getIMDBdata(newData[0].id);
            let tempData = newData[0];
            tempData = { ...tempData, IMDBdata: IMDBdata }
            FinishedData.push(tempData);
        } catch (err) {
            console.log(unique[i].name);
        }
    }

    // Now finishedData is the list of movies from folders
    let temp_state = [...allFolders];
    let temp_element = { ...allFolders[index] };
    temp_element.FinishedData = FinishedData;
    temp_state[index] = temp_element;
    setAllFolders(temp_state);
    ls.set('movies', temp_state);
}

async function updateBoth(allFolders, setAllFolders, array, folderName, setLoading, index, smallId) {
    setLoading(true);
    const Names = [];
    let Name;
    let Date;

    for (let i = 0; i < array.length; i++) {
        Name = array[i].name.split('.')[0].split('(')[0];
        Date = array[i].name.split('.')[0].replace(/\D+/g, '');
        let trimmedName = Name.replace(/\s+$/, '');
        let trimmedDate = Date.replace(/\s+$/, '');
        let newData = {
            name: trimmedName,
            date: trimmedDate,
        }
        Names.push(newData);
    }
    let unique = Names.filter((ele, ind) => ind === Names.findIndex(elem => elem.name === ele.name))
    updateFetchRequest(unique, setAllFolders, allFolders, folderName, setLoading, index, smallId);
}

// Uses name AND date to find the movies via the api
const updateFetchRequest = async (unique, setAllFolders, allFolders, folderName, setLoading, index, smallId) => {
    let FinishedData = [];
    for (let i = 0; i < unique.length; i++) {
        try {
            const response = await fetch(`https://api.themoviedb.org/3/search/movie?query=${unique[i].name}&api_key=a3dbbc2d7d1c0929ba88f4887b98b5ca`);
            const data = await response.json();
            let newData = data.results.filter(Item => Item.release_date.includes(unique[i].date));
            if (newData.length !== 1) {
                newData = data.results.filter(Item => Item.title === unique[i].name && Item.release_date.includes(unique[i].date));
            }
            const IMDBdata = await getIMDBdata(newData[0].id);
            let tempData = newData[0];
            tempData = { ...tempData, IMDBdata: IMDBdata }
            FinishedData.push(tempData);
        } catch (err) {
            console.log(unique[i].name);
        }
    }
    // Now finishedData is the list of movies
    const date = format(new Date(), 'M/dd/yyyy');
    const ReadyData = {
        id: smallId,
        folderName: folderName,
        date: date,
        FinishedData
    }

    let temp_state = [...allFolders];
    let temp_element = { ...allFolders[index] };
    temp_element = ReadyData;
    temp_state[index] = temp_element;
    setAllFolders(temp_state);
    setAllFolders(temp_state);
    ls.set('movies', temp_state);
    setLoading(false);
}

async function getIMDBdata(id) {
    let IMDBdata = [];
    try {
        const getImdbid = await fetch(`https://api.themoviedb.org/3/movie/${id}/external_ids?api_key=a3dbbc2d7d1c0929ba88f4887b98b5ca`);
        const imdbData = await getImdbid.json();
        const response = await fetch(`https://imdb-api.com/en/API/Title/k_vcyqkw6f/${imdbData.imdb_id}`);
        const data = await response.json();
        IMDBdata = {
            rating: data.imDbRating,
            runtime: data.runtimeStr,
        }
        return IMDBdata;
    }
    catch (err) {
        console.log('error fetching IMDB data')
    }
}