import React from 'react';
import { ContextFunction } from '../../Context/ContextProvider';
import ls from 'local-storage';
import Modal from 'react-modal';
import '../form.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};

Modal.setAppElement(document.getElementById('root'));

function DeleteModal({ deleteIsOpen, setDeleteIsOpen }) {
    const obj = ContextFunction();
    const { allFolders, setAllFolders } = obj;

    let subtitle2;

    function closeDelete() {
        setDeleteIsOpen({
            open: false,
            name: '',
            index: 0,
        });
    }

    const deleteFolder = (index) => {
        const reducedArr = [...allFolders];
        reducedArr.splice(index, 1);
        setAllFolders(reducedArr);
        ls.set('movies', reducedArr);
        closeDelete();
    }

    return (
        <Modal
            className='Modal'
            isOpen={deleteIsOpen.open}
            onRequestClose={closeDelete}
            style={customStyles}
            overlayClassName="Overlay"
            contentLabel="Example Modal"
        >
            <h2 ref={(_subtitle) => (subtitle2 = _subtitle)} className={'headerH3'}>Delete {deleteIsOpen.name}</h2>
            <form className={'flexCenter'}>
                <div className={'white'}>Are you sure you want to delete {deleteIsOpen.name}?</div>
                <div className='buttonsContainer'>
                    <div className='edit' onClick={closeDelete}>Cancel</div>
                    <div className='delete' onClick={() => deleteFolder(deleteIsOpen.index)}>Delete</div>
                </div>
            </form>
        </Modal >
    );

}

export default DeleteModal;
