import React from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { ContextFunction } from '../Context/ContextProvider';
import ls from 'local-storage';
import Modal from 'react-modal';
import styles from './DragList.module.css';

Modal.setAppElement(document.getElementById('root'));

function DragList({ openEdit, openDelete }) {
    const obj = ContextFunction();
    const { allFolders, setAllFolders } = obj;

    const onDragEnd = (result) => {
        if (!result.destination) return;
        let NewData;
        let Arr = Array.from(allFolders);
        let [removed] = Arr.splice(result.source.index, 1);
        Arr.splice(result.destination.index, 0, removed);
        NewData = Arr;
        setAllFolders(NewData);
        ls.set('movies', NewData);
    }

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="droppable">
                {(provided, snapshot) => (
                    <div {...provided.droppableProps}
                        ref={provided.innerRef}>
                        {
                            allFolders.map((item, index) =>
                                <Draggable draggableId={item.id} index={index} key={item.id}>
                                    {(provided, snapshot) => (
                                        <li ref={provided.innerRef}
                                            {...provided.draggableProps}
                                            {...provided.dragHandleProps} className={styles.editContainer}>
                                            <div className={styles.item}>{item.folderName}</div>
                                            <div className={styles.item}>{allFolders[index].FinishedData.length}</div>
                                            <div className={styles.item}>{item.date}</div>
                                            <div className={`${styles.item} ${styles.buttonBox}`}>
                                                <div className={styles.edit} onClick={() => openEdit(item, index)}>Edit</div>
                                                <div className={styles.delete} onClick={() => openDelete(item, index)}>Delete</div>
                                            </div>
                                        </li>
                                    )}
                                </Draggable>
                            )}
                        {provided.placeholder}
                    </div>
                )}
            </Droppable>
        </DragDropContext>
    );
}

export default DragList;