
import React from 'react';
import Modal from 'react-modal';
import '../form.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};

Modal.setAppElement(document.getElementById('root'));

function AddModal({ onChange, onTextChange, submitForm, loading, success, setSuccess }) {
    let subtitle;
    const [modalIsOpen, setIsOpen] = React.useState(false);

    function openModal() {
        setIsOpen(true);
    }

    function afterOpenModal() {
        subtitle.style.color = '#f00';
    }

    function closeModal() {
        setIsOpen(false);
        setSuccess({
            success: false,
            itemCount: 0,
        })
    }

    function SubmitEverything(e) {
        e.preventDefault();
        submitForm();
        closeModal();
    }

    return (
        <div className={'modalContainerBox'}>
            <div className={'add'} onClick={openModal}>Add New Directory</div>
            <Modal
                className="Modal"
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                overlayClassName="Overlay"
                contentLabel="Example Modal"
            >
                <h2 ref={(_subtitle) => (subtitle = _subtitle)} className={'HeaderH2'}>Add New Directory</h2>
                <form onSubmit={SubmitEverything} className={'flexCenter'}>
                    <div className='formContainer'>
                        <div className={'textContainer'}>
                            <label htmlFor="text" className={'nameText'}>
                                Name:
                            </label>
                            <input type="text" required onChange={onTextChange} placeholder={'Name'} className='nameInput' />
                        </div>
                        <div className="imageUploadContainer">
                            <label title='Select Directory' htmlFor="file-input" className={'fileText'}>
                                <img src={require('../../assets/upload.png')} className={'imageUpload'}></img>
                                {
                                    success.success ? <div className='success'>Selected {success.itemCount} items</div> : <div className='text'>Select Directory</div>
                                }
                            </label>
                            <input type="file" id="file-input" required multiple onChange={onChange} />
                        </div>
                    </div>
                    {
                        loading ? <img src={require('../../assets/loading.gif')} className={'imageLoading'}></img> : <></>
                    }
                    <div className='buttonsContainer'>
                        <div className='delete' onClick={closeModal}>Cancel</div>
                        <input type={'submit'} className={`${loading ? 'disabled-edit' : 'edit'}`} value="Submit" />
                    </div>
                </form>
            </Modal>
        </div>
    );
}

export default AddModal;