import React, { useState } from 'react';
import { ContextFunction } from '../Context/ContextProvider';
import fetchData from '../hooks/fetch';
import styles from './Settings.module.css';
import DeleteModal from '../Components/forms/delete';
import EditModal from '../Components/forms/edit';
import AddModal from '../Components/forms/add';
import DragList from '../Components/DragList';

function Settings() {
    const obj = ContextFunction();
    const { allFolders, setAllFolders } = obj;

    const [loading, setLoading] = useState(false);
    const [folderName, setFolderName] = useState('');
    const [file, setFile] = useState([]);
    const [success, setSuccess] = useState({
        success: false,
        itemCount: 0,
    });
    const [editIsOpen, setEditIsOpen] = useState({
        open: false,
        name: '',
        index: 0,
        smallId: 0,
    });
    const [deleteIsOpen, setDeleteIsOpen] = useState({
        open: false,
        name: '',
    });


    function openEdit(item, index) {
        setEditIsOpen({
            open: true,
            name: item.folderName,
            index: index,
            smallId: item.id
        });
    }

    function openDelete(item, index) {
        setDeleteIsOpen({
            open: true,
            name: item.folderName,
            index: index,
        });
    }

    const onChange = (e) => {
        let folder = e.target.files
        setFile(folder);
        setSuccess({
            success: true,
            itemCount: folder.length,
        });
    }

    const onTextChange = (e) => {
        let text = e.target.value;
        setFolderName(text);
    }

    const submitForm = async () => {
        fetchData(allFolders, setAllFolders, file, folderName, setLoading);
    }

    return (
        <div className={styles.settingsContainer}>
            <div className={styles.settingsContainerChild}>
                <h1 className={styles.header}>Directories</h1>
                <ul className={styles.listContainer}>
                    <li className={styles.editContainer2}>
                        <div className={styles.item2}>Name</div>
                        <div className={styles.item2}>Movies</div>
                        <div className={styles.item2}>Date Added</div>
                        <div className={styles.item2}>Actions</div>
                    </li>
                    <DragList openDelete={openDelete} openEdit={openEdit} />
                    {
                        loading ? <img src={require('../assets/loading.gif')} className={styles.imageLoading}></img> : <></>
                    }
                    <EditModal editIsOpen={editIsOpen} setEditIsOpen={setEditIsOpen} loading={loading} setLoading={setLoading} />
                    <DeleteModal deleteIsOpen={deleteIsOpen} setDeleteIsOpen={setDeleteIsOpen} />
                    <AddModal onChange={onChange} onTextChange={onTextChange} submitForm={submitForm} loading={loading} success={success} setSuccess={setSuccess} />
                </ul>
            </div>
        </div>
    );

}

export default Settings;